<template>
  <div style="margin-top: 20px; display: inline-block; width: 80vw">
    <h1 class="h3 mb-3 fw-normal">로그인</h1>

    <div class="form-floating">
      <input
        type="text"
        class="form-control"
        id="floatingInput"
        placeholder="name@example.com"
        v-model="username"
      />
      <label for="floatingInput">이메일 또는 아이디</label>
    </div>
    <div class="form-floating">
      <input
        type="password"
        class="form-control"
        id="floatingPassword"
        placeholder="Password"
        v-model="password"
      />
      <label for="floatingPassword">비밀번호</label>
    </div>
    <div style="margin-top: 36px">
      <span>{{ msg }}</span>
    </div>

    <button
      class="w-90 btn btn-lg mt-5"
      style="color: #fff"
      type="submit"
      @click="login"
    >
      로그인
    </button>
    <p class="mt-5 mb-3 text-muted">&copy; khs</p>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "loginView",
  components: {},
  data() {
    return {
      username: "",
      password: "",
      msg: "",
    };
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    async login() {
      await axios
        .post("/api/auth/local", {
          identifier: this.username,
          password: this.password,
        })
        .then((res) => {
          localStorage.setItem("jwt", res.data.jwt);
          this.$router.push({ name: "home" });
          this.$store.commit("loginSuccess", res.data.user);
        })
        .catch(() => {
          this.msg = "아이디 또는 비밀번호가 일치하지 않습니다.";
        });
    },
  },
};
</script>
<style scoped></style>
